header{
   background-color: rgb(124, 189, 167);
   display: flex;
   justify-content:space-between;
   height: 15vh;
   align-items:center;
   text-transform: uppercase;
   color:rgb(19, 97, 97);


}
h1{
    margin-left: 20px;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
}
a{
    padding: 10px;
    margin-right: 10px;
    text-transform: uppercase;
    text-decoration: none;
    cursor: pointer;
    color: white;
    font-weight: 700;
}
a:hover{
    color: rgb(19, 97, 97);
}