*{
margin: 0;
}
body{
    background-color: rgb(19, 97, 97);
}
.heading{
    position:absolute;
    top: 50%;
    left: 50%;
    transform:translate(-50% ,-50%);
    
  
    width: 250px;

}
.heading h1{
    padding: 10px;
    color: rgb(124, 189, 167);
    font-weight: 900;
    text-align: center;
}
.heading input{
    padding: 15px;
    width: 100%;
    border-radius: 20px;
    background:transparent;
    border: 2px solid rgb(124, 189, 167);
    outline: none;
    text-align: center;
    color:rgb(124, 189, 167) ;
    font-size: medium;
    font-weight: 700;
    box-sizing: border-box;
}
button{
    text-align: center;
    margin: 30px 0 0 0px;
    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    background: transparent;
    width: 100px;
    height: 30px;
    border-radius: 20px;
    color:rgb(124, 189, 167) ;
    border: 1px solid greenyellow;
    cursor: pointer;

}
button:hover{
    background-color:rgb(124, 189, 167) ;
    color: rgb(20, 20, 19);
}
.content{
    width: 100%;
   
    display: flex;
    align-items: center;
    justify-content: left;
    margin-top: 0px;
    padding: 0%;


}
ul{
 
  width: 100%;
   padding: 0;
   text-align:center;
 
    
}
ul li{
    text-decoration: none;
    padding: 20px;
    border: 1px solid rgb(124, 189, 167);
    margin: 20px 0 20px 0;
    border-radius: 20px;
    font-weight: 700;
  color:rgb(124, 189, 167) ;
  list-style: none;
  
}
i{
    float: right;
    cursor: pointer;
    padding: 5px;
}
i:hover{
    color: rgb(148, 54, 54);
}